  background-color: var(--clr-categoty-btn);
}

.others__container {
  position: relative;
}

.others__text-btn {
  font-size: 12px;
  line-height: 1.33333;
}

.others__icon-open {
  fill: var(--clr-categoty-btn);
  display: block;
}

.others__icon-close {
  fill: var(--clr-categoty-btn-background);
  display: none;
}

.others__list {
  width: 168px;
  height: 380px;
  overflow: overlay;
  background: var(--clr-categoty-btn-background);
  filter: drop-shadow(0 10px 60px rgba(0, 0, 0, .1));
  border-radius: 20px;
  padding: 14px 24px;
}

.others__item:not(:last-child) {
  margin-bottom: 12px;
}

.others__item-btn {
  cursor: pointer;
  color: var(--clr-categoty-btn);
  letter-spacing: .01em;
  text-align: left;
  background-color: rgba(0, 0, 0, 0);
  font-size: 12px;
  line-height: 1.33333;
  display: inline-block;
}

.others__item-btn:hover, .others__item-btn:focus {
  -webkit-text-decoration-line: underline;
  text-decoration-line: underline;
}

.others__wrapper {
  z-index: 2;
  pointer-events: none;
  visibility: hidden;
  position: absolute;
  top: 100%;
  left: 0;
}

.others__wrapper.is-open {
  pointer-events: auto;
  visibility: visible;
}

.others__wrapper.is-open ~ .others__btn {
  background-color: var(--clr-categoty-btn);
  color: var(--clr-categoty-btn-background);
}

.others__wrapper.is-open ~ .others__btn .others__icon-open {
  display: none;
}

.others__wrapper.is-open ~ .others__btn .others__icon-close {
  display: block;
}

